/* ===================== Imports ==================== */
import React from 'react'; // import React to create React component
import './App.css'; // import the CSS file
//import greenFlag from './static/img/greenFlag.gif'; // import gif image from the 'src' folder
import backImg from './static/img/background.jpg'
import yankee from './static/img/Yankee_Division.svg'
import token from './static/img/pilgrimageToken.png'
import about from './static/img/about.svg'
import idCard from './static/img/IDcard.svg'
/* ================================================== */

function PXtoVH(px) {
  return ( px * ( 100 / window.innerHeight) );
}

class App extends React.Component {
  constructor() {
    super();
    this.state={
      links: ["#", "#", "#", "#"],
      images: [yankee, token, about, idCard],
      texts: ["THE 26TH YANKEE DIVISION", "VIRTUAL PILGRIMAGE", "ABOUT US", "CONTACT US"],
      windowHeight: 0,
      scrollIndex: 0,
      scrollPos: 0,
      relativePos: [0, 0, 0, 0, 0, 0],
      gridSize: 100/6,
      styles: {
        container: {
          width: "100vw",
          height: "100vh",
          overflow: "hidden",
          display: "grid",
          fontSize: "3vw",
        },
        menuImg1: {
          zIndex: "0",
          transition: "opacity 0.3s ease-in-out 0.1s",
          opacity: "0",
          width: "10vw",
          paddingLeft: "2vw",
        },
        menuImg2: {
          zIndex: "0",
          transition: "opacity 0.3s ease-in-out 0.1s",
          opacity: "0",
          width: "10vw",
          paddingLeft: "2vw",
          transform: "rotateY(180deg)",
        },
        background: {
          opacity: "100%",
          position: "absolute",
          width: "100vw",
          height: "100vh",
          top: "0",
          left: "0",
          padding: "0",
          margin: "0",
          filter: "brightness(50%)",
          zIndex: "-1",
        }
      }
    };
  }

  componentDidMount() {
    var windowHeight = PXtoVH(window.innerHeight);
    var container = document.querySelector('.container');
    container.addEventListener("wheel", event => {
      this.scrollHandle(event.wheelDelta, event.deltaY, windowHeight)
    }, { passive: true});
  }

  componentWillUnmount() {
    var windowHeight = PXtoVH(window.innerHeight);
    var container = document.querySelector('.container');
    container.addEventListener("wheel", event => {
      this.scrollHandle(event.wheelDelta, event.deltaY, windowHeight)
    }, { passive: true});
  }

  /*hoverEvent = () => {

  }*/

  scrollHandle = (wheelDelta, deltaY, innerHeight) => {
    var { scrollIndex, windowHeight, gridSize } = this.state;

    this.setState({
      scrollIndex: scrollIndex + PXtoVH(Math.round(wheelDelta/2)),
      scrollPos: Math.sign(deltaY),
      windowHeight: innerHeight,
    });

    if ( windowHeight - (gridSize * 2) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight - gridSize ) {
      this.setState({
        relativePos: [gridSize*7, 0, 0, 0, 0, 0],
      });
    } else if ( windowHeight - (gridSize * 3) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight - (gridSize * 2) ) {
      this.setState({
        relativePos: [gridSize*7, gridSize*7, 0, 0, 0, 0],
      });
    } else if ( windowHeight - (gridSize * 4) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight - (gridSize * 3) ) {
      this.setState({
        relativePos: [gridSize*7, gridSize*7, gridSize*7, 0, 0, 0],
      });
    } else if ( windowHeight - (gridSize * 5) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight - (gridSize * 4) ) {
      this.setState({
        relativePos: [gridSize*7, gridSize*7, gridSize*7, gridSize*7, 0, 0],
      });
    } else if ( windowHeight - (gridSize * 6) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight - (gridSize * 5) ) {
      this.setState({
        relativePos: [gridSize*7, gridSize*7, gridSize*7, gridSize*7, gridSize*7, 0],
      });
    } else if ( windowHeight - (gridSize * 7) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight - (gridSize * 6) ) {
      this.setState({
        relativePos: [gridSize*7, gridSize*7, gridSize*7, gridSize*7, gridSize*7, gridSize*7],
      });
    } else if ( windowHeight + gridSize < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight + (gridSize * 2) ) {
      this.setState({
        relativePos: [0, 0, 0, 0, 0, -gridSize*7],
      });
    } else if ( windowHeight + (gridSize * 2) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight + (gridSize * 3) ) {
      this.setState({
        relativePos: [0, 0, 0, 0, -gridSize*7, -gridSize*7],
      });
    } else if ( windowHeight + (gridSize * 3) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight + (gridSize * 4) ) {
      this.setState({
        relativePos: [0, 0, 0, -gridSize*7, -gridSize*7, -gridSize*7],
      });
    } else if ( windowHeight + (gridSize * 4) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight + (gridSize * 5) ) {
      this.setState({
        relativePos: [0, 0, -gridSize*7, -gridSize*7, -gridSize*7, -gridSize*7],
      });
    } else if ( windowHeight + (gridSize * 5) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight + (gridSize * 6) ) {
      this.setState({
        relativePos: [0, -gridSize*7, -gridSize*7, -gridSize*7, -gridSize*7, -gridSize*7],
      });
    } else if ( windowHeight + (gridSize * 6) < windowHeight + scrollIndex && windowHeight + scrollIndex <= windowHeight + (gridSize * 7) ) {
      this.setState({
        relativePos: [-gridSize*7, -gridSize*7, -gridSize*7, -gridSize*7, -gridSize*7, -gridSize*7],
      });
    } else {
      if ( windowHeight - (gridSize * 7) > windowHeight + scrollIndex || windowHeight + scrollIndex > windowHeight + (gridSize * 7)) {
        this.setState({
          relativePos: [0, 0, 0, 0, 0, 0],
          scrollIndex: 0,
        });
      } else {
        this.setState({
          relativePos: [0, 0, 0, 0, 0, 0],
        });
      }
    }
  }

  render() {
    var { scrollIndex, styles, relativePos, links, texts, images } = this.state;
    return (
      <div className="container" style={styles.container}>
        <a key="0" id="yankee" href={links[0]} style={{transform: `translate(-2vw,${scrollIndex + relativePos[0]}vh)`}}>
          <span>{texts[0]}<img src={images[0]} alt="menuImg" style={styles.menuImg1} /></span>
        </a>
        <a key="1" id="pilgrimage" href={links[1]} style={{transform: `translate(4vw,${scrollIndex + relativePos[1]}vh)`}}>
          <span><img src={images[1]} alt="menuImg" style={styles.menuImg2} />{texts[1]}</span>
        </a>
        <a key="2" id="about" href={links[2]} style={{transform: `translate(-4.5vw,${scrollIndex + relativePos[2]}vh)`}}>
          <span>{texts[2]}<img src={images[2]} alt="menuImg" style={styles.menuImg1} /></span>
        </a>
        <a key="3" id="contact" href={links[3]} style={{transform: `translate(7vw,${scrollIndex + relativePos[3]}vh)`}}>
          <span><img src={images[3]} alt="menuImg" style={styles.menuImg2} />{texts[3]}</span>
        </a>
        <img id="backImg" src={backImg} alt="background" style={styles.background} />
      </div>
    );
  }
}

export default App;